@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --primary-color: #335eea;
  --secondary-color: #eaeffd;
}

body {
  background: #ffffff;
  margin: 0;
  font-family: "Poppins";
}

.section-color {
    background: #F1F4F8;
}

.navbar {
  background: white;
  padding-top: 1.6rem;
}

.navbar .logo {
  text-decoration: none;
  font-weight: bold;
  color: black;
  padding-left: 2em;
  font-size: 1.2em;
}

.navbar .logo span {
  color: #335eea;
  color: var(--primary-color);
}

.left-col h1 span {
  color: #335eea;
  color: var(--primary-color);
}

.navbar nav {
  display: none;
}

.navbar .container {
  display: flex;
  place-content: space-between;
}

.navbar .mobile-menu {
  cursor: pointer;
}

a {
  color: #444444;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

section {
  padding: 2em 2em;
}

.hero {
  text-align: center;
}

.left-col .subhead {
  font-weight: 500;
  color: rgb(129, 128, 128);
  letter-spacing: 0.2em;
}

.left-col h1 {
  font-size: 1.9em;
  margin-top: 0.1em;
}

.left-col .primary-cta {
  background: #335eea;
  background: var(--primary-color);
  color: white;
  margin-right: 1em;
  text-decoration: none;
  padding: 0.8em 1.1em;
  font-size: 1.1em;
  border-radius: 0.5em;
  font-weight: 500;
  display: inline-block;
}

.bta {
  background: #eaeffd;
  background: var(--secondary-color);
  color: #335eea;
  color: var(--primary-color);
  text-decoration: none;
  padding: 0.6em 1.1em;
  font-size: 1.1em;
  border-radius: 0.5em;
  font-weight: 500;
  display: inline-block;
}

.smallhead {
  color: #20272e;
  font-weight: 500;
}

.hero-img {
  width: 60%;
  margin-top: 2em;
}

.hero-img-2 {
    width: 80%;
    margin-top: 2em;
  }
  

section.features-section {
  background: #20272e;
  color: white;
}

ul.features-list {
  margin: 0;
  padding-left: 0.1em;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: (minmax(19rem, 1fr)) [auto-fit];
  grid-template-columns: repeat(auto-fit, minmax(19rem, 1fr));
}

ul.features-list li {
  font-size: 1.1em;
  margin-bottom: 1em;
  margin-left: 2em;
  position: relative;
}

ul.features-list li:before {
  content: "";
  left: -2em;
  position: absolute;
  width: 20px;
  height: 20px;
  background-image: url(/static/media/bullet.e0ed9094.svg);
  background-size: contain;
  margin-right: 0.5em;
}

.features-section img {
  display: none;
}

.testimonials-section {
  background: white;
  color: gray;
}

.testimonials-section li {
  background: white;
  text-align: start;
  padding: 1em 1em;
  width: 80%;
  margin: 0 auto 0em auto;
}

.testimonials-section li img {
  width: 4em;
  height: 4em;
  margin-top: 0em;
}

h2 {
  font-size: 2em;
}

label {
  display: block;
  font-size: 1.2em;
  margin-bottom: 0.5em;
}

nav.menu-btn {
  display: block;
}

nav {
  position: fixed;
  z-index: 999;
  width: 66%;
  right: 0;
  top: 0;
  background: #20272e;
  height: 100vh;
  padding: 1em;
}

nav ul.primary-nav {
  margin-top: 5em;
}

nav li a {
  color: white;
  text-decoration: none;
  display: block;
  padding: 0.5em;
  font-size: 1.3em;
  text-align: right;
}

nav li a:hover {
  font-weight: bold;
}

.mobile-menu-exit {
  float: right;
  margin: 0.5em;
  cursor: pointer;
}
.hero .container .left-col h1 {
    font-size: 2.2em;
    font-weight: 500;
    line-height: 1.4em;
    width: 100%;
  }
  .hero-img-trans {
    width: 65%;
    perspective: 1500px;
    transform-style: preserve-3d;
    transform: rotateY(-35deg) rotateX(15deg);
    box-shadow: 25px 60px 125px -25px rgb(80 102 144 / 10%), 16px 40px 75px -40px rgb(0 0 0 / 20%);
    margin-top: 0em;
  } 
@media only screen and (min-width: 768px) {
  .mobile-menu,
  .mobile-menu-exit {
    display: none;
  }
  .navbar .container {
    display: -ms-grid;
    display: grid;
    -ms-grid-columns: 180px auto;
    grid-template-columns: 180px auto;
    justify-content: unset;
  }
  .navbar nav {
    display: flex;
    justify-content: space-around;
    background: none;
    position: unset;
    height: auto;
    width: 100%;
    padding: 0;
  }
  .navbar nav ul {
    display: flex;
  }
  .navbar nav a {
    color: black;
    font-size: 1em;
    padding: 0.1em 1em;
  }
  .navbar nav ul.primary-nav {
    margin: 0;
  }
  .navbar nav li.current a {
    font-weight: bold;
  }
  .navbar nav li.go-premium-cta a {
    background: #335eea;
    background: var(--primary-color);
    color: white;
    font-weight: 500;
    padding: 0.8em;
    border-radius: 0.5em;
    margin-top: -0.2em;
  }
  .navbar nav li.go-premium-cta a:hover {
    background: white;
    color: #335eea;
    color: var(--primary-color);
  }
}

@media only screen and (min-width: 1080px) {
  .container {
    width: 1080px;
    margin: 0 auto;
  }
  section {
    padding: 5em 4em 0em;
  }
  .hero .container {
    display: flex;
    justify-content: space-between;
    text-align: left;
  }
  .hero .container .left-col {
    margin: 3em 1em 0em 2em;
  }
  .hero .container .left-col h1 {
    font-size: 2.2em;
    font-weight: 500;
    line-height: 1.4em;
    width: 100%;
  }
  .hero-img {
    width: 59%;
    margin-top: 0em;
  }
  .hero-img-2 {
    width: 30%;
    margin-top: 2em;
  }
  .hero-img-trans {
    width: 60%;
    perspective: 1500px;
    transform-style: preserve-3d;
    transform: rotateY(-35deg) rotateX(15deg);
    box-shadow: 25px 60px 125px -25px rgb(80 102 144 / 10%), 16px 40px 75px -40px rgb(0 0 0 / 20%);
    margin-top: 0em;
  }
  .hero-cta {
    display: flex;
  }

  ul.features-list {
    display: block;
    margin-left: 5em;
  }
  ul.features-list li {
    font-size: 1.4em;
  }
  ul.features-list li:before {
    width: 30px;
    height: 30px;
  }
  .features-section {
    position: relative;
  }
  .features-section img {
    display: block;
    position: absolute;
    right: 0;
    width: 350px;
    bottom: -2em;
  }
  .testimonials-section ul {
    display: flex;
  }
  .testimonials-section ul li {
    margin: 0 1em;
  }
}

.footer {
    display: flex;
    flex-flow: row wrap;
    padding: 50px 5em 100px 8em;
    color: #2f2f2f;
    background-color: #F1F4F8;
  }
  .footer > * {
    flex:  1 1 100%;
  }
  .footer__logo {
    font-weight: bold;
    color: #335eea;
    font-size: 1.5rem;
  }
  
  .footer__addr h2 {
    margin-top: 0em;
    font-size: 15px;
    font-weight: 500;
  }
  
  .nav__title {
    font-weight: 400;
    font-size: 15px;
  }
  
  .footer address {
    font-style: normal;
    color: #999;
  }
  
  .footer__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 36px;
    max-width: -webkit-max-content;
    max-width: -moz-max-content;
    max-width: max-content;
    background-color: rgb(33, 33, 33, 0.07);
    border-radius: 100px;
    color: #2f2f2f;
    line-height: 0;
    margin: 0.6em 0;
    font-size: 1rem;
    padding: 0 1.3em;
  }
  
  .footer ul {
    list-style: none;
    padding-left: 0;
  }
  
  .footer li {
    line-height: 2em;
  }
  
  .footer a {
    text-decoration: none;
  }
  
  .footer__nav {
    display: flex;
      flex-flow: row wrap;
  }
  
  .footer__nav > * {
    flex: 1 1 50%;
    margin-right: 1.25em;
  }
  
  .nav__ul a {
    color: #999;
  }
  
  @media screen and (min-width: 24.375em) {
    .legal .legal__links {
      margin-left: auto;
    }
  }
  
  @media screen and (min-width: 40.375em) {
    .footer__nav > * {
      flex: 1 1;
    }
    
    .nav__item--extra {
      flex-grow: 2;
    }
    
    .footer__addr {
      flex: 1 1;
    }
    
    .footer__nav {
      flex: 2 1;
    }
  }


.background::after {
  content: '';
  background: #60a9ff;
  background: linear-gradient(to bottom, #60a9ff 0%,#4394f4 100%);
  height: 350px;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 1;
}
@media (min-width: 900px) {
  .background {
    padding: 0 0 25px;
  }
}

.container-pricing {
  margin: 0 auto;
  padding: 50px 0 0;
  max-width: 960px;
  width: 100%;
}

.panel {
  background-color: #fff;
  border-radius: 10px;
  padding: 15px 25px;
  position: relative;
  width: 100%;
  z-index: 10;
}

.pricing-table {
  box-shadow: 0px 10px 13px -6px rgba(0, 0, 0, 0.08), 0px 20px 31px 3px rgba(0, 0, 0, 0.09), 0px 8px 20px 7px rgba(0, 0, 0, 0.02);
  display: flex;
  flex-direction: column;
}

@media (min-width: 900px) {
  .pricing-table {
    flex-direction: row;
  }
}

.pricing-table * {
  text-align: center;
  text-transform: uppercase;
}

.pricing-plan {
  border-bottom: 1px solid #e1f1ff;
  padding: 25px;
}

.pricing-plan:last-child {
  border-bottom: none;
}

@media (min-width: 900px) {
  .pricing-plan {
    border-bottom: none;
    border-right: 1px solid #e1f1ff;
    flex-basis: 100%;
    padding: 25px 50px;
  }

  .pricing-plan:last-child {
    border-right: none;
  }
}

.pricing-img {
  margin-bottom: 25px;
  max-width: 100%;
}

.pricing-header {
  color: #888;
  font-weight: 400;
  font-size: 18px;
}

.pricing-features {
  color: #016FF9;
  font-weight: 600;
  letter-spacing: 1px;
  margin: 50px 0 25px;
}

.pricing-features-item {
  border-top: 1px solid #e1f1ff;
  font-size: 12px;
  line-height: 1.5;
  padding: 15px 0;
}

.pricing-features-item:last-child {
  border-bottom: 1px solid #e1f1ff;
}

.pricing-price {
  color: black;
  font-size: 4em;
  font-weight: 500;
}

.pricing-button {
  align-self: flex-end;
  border: 1px solid #9dd1ff;
  border-radius: 10px;
  color: #348EFE;
  display: inline-block;
  margin: 25px 0;
  padding: 15px 35px;
  text-decoration: none;
  transition: all 150ms ease-in-out;
}

.pricing-button:hover,
.pricing-button:focus {
  background-color: #e1f1ff;
}

.pricing-button.is-featured {
  background-color: #48aaff;
  color: #fff;
}

.pricing-button.is-featured:hover,
.pricing-button.is-featured:active {
  background-color: #269aff;
}

